import Image from 'next/image';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { PlaceHolderImages } from '@/lib/placeholder-images';
import { products } from '@/lib/data';
import { ArrowRight } from 'lucide-react';

export default function Home() {
  const heroImage = PlaceHolderImages.find((img) => img.id === 'hero-1');
  const featuredProducts = products.slice(0, 3).map((product) => {
    const image = PlaceHolderImages.find((img) => img.id === product.imageId);
    return { ...product, imageUrl: image?.imageUrl, imageHint: image?.imageHint };
  });

  return (
    <div className="flex flex-col min-h-dvh">
      <main className="flex-1">
        <section className="relative w-full h-[60vh] md:h-[80vh] flex items-center justify-center text-center">
          {heroImage && (
            <Image
              src={heroImage.imageUrl}
              alt={heroImage.description}
              fill
              className="object-cover object-center"
              priority
              data-ai-hint={heroImage.imageHint}
            />
          )}
          <div className="absolute inset-0 bg-black/40" />
          <div className="relative z-10 p-4 text-white">
            <h1 className="text-4xl md:text-6xl lg:text-7xl font-headline font-bold drop-shadow-lg text-balance">
              Crafted with Love, Baked to Perfection
            </h1>
            <p className="mt-4 max-w-2xl mx-auto text-lg md:text-xl drop-shadow">
              Discover the finest handcrafted cakes, cupcakes, and delectables for all your special moments.
            </p>
            <Button asChild size="lg" className="mt-8 bg-primary text-primary-foreground hover:bg-primary/90">
              <Link href="/custom-order">Request a Custom Order</Link>
            </Button>
          </div>
        </section>

        <section id="about" className="py-16 md:py-24">
          <div className="container mx-auto px-4">
            <div className="grid md:grid-cols-2 gap-8 md:gap-16 items-center">
              <div className="prose prose-lg dark:prose-invert max-w-none">
                <h2 className="text-3xl md:text-4xl font-headline">Welcome to Rue's Delectables</h2>
                <p className="text-balance">
                  At Rue's, we believe every celebration deserves a touch of sweetness. Founded from a passion for baking and an artist's eye for detail, our little bakery has blossomed into a local favorite. We use only the finest ingredients to create treats that are as delightful to look at as they are to eat.
                </p>
                <p>
                  From elegant wedding cakes to whimsical birthday cupcakes, each creation is a unique masterpiece. Let us be a part of your story and make your next event unforgettably delicious.
                </p>
              </div>
               <div className="relative aspect-square max-w-md mx-auto">
                 <Image 
                  src="https://picsum.photos/seed/about1/500/500" 
                  alt="A smiling baker holding a cake" 
                  width={500}
                  height={500}
                  className="rounded-lg shadow-lg"
                  data-ai-hint="smiling baker"
                  />
              </div>
            </div>
          </div>
        </section>

        <section id="featured" className="py-16 md:py-24 bg-card">
          <div className="container mx-auto px-4">
            <div className="text-center mb-12">
              <h2 className="text-3xl md:text-4xl font-headline">Our Signature Treats</h2>
              <p className="mt-2 text-lg text-muted-foreground">A taste of what we do best</p>
            </div>
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
              {featuredProducts.map((product) => (
                <Card key={product.id} className="overflow-hidden">
                  {product.imageUrl && (
                    <div className="relative aspect-video">
                      <Image
                        src={product.imageUrl}
                        alt={product.name}
                        fill
                        className="object-cover"
                        data-ai-hint={product.imageHint}
                      />
                    </div>
                  )}
                  <CardHeader>
                    <CardTitle className="font-headline text-2xl">{product.name}</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <CardDescription>{product.description}</CardDescription>
                  </CardContent>
                </Card>
              ))}
            </div>
            <div className="text-center mt-12">
              <Button asChild variant="outline">
                <Link href="/products">View All Products <ArrowRight className="ml-2 h-4 w-4" /></Link>
              </Button>
            </div>
          </div>
        </section>

        <section id="cta" className="py-16 md:py-24">
          <div className="container mx-auto px-4 text-center">
            <h2 className="text-3xl md:text-4xl font-headline">Have a Vision?</h2>
            <p className="mt-2 text-lg text-muted-foreground max-w-2xl mx-auto">
              We specialize in bringing your ideas to life. Let's collaborate on a custom creation for your next event.
            </p>
            <Button asChild size="lg" className="mt-8 bg-primary text-primary-foreground hover:bg-primary/90">
              <Link href="/custom-order">Start Your Custom Order</Link>
            </Button>
          </div>
        </section>
      </main>
    </div>
  );
}
